import React, { useEffect, useState } from "react";
import {
    Container,
    TextField,
    Grid,
    FormControl,
    FormLabel,
    RadioGroup,
    FormControlLabel,
    Radio,
    Button,
} from "@mui/material";

function IdEditor(props) {
    const { changeInfo, gotoQrcode } = props;

    const [userName, setName] = useState("");
    const [userId, setId] = useState({ front: "", back: "" });
    const [gender, setGender] = useState("男");
    const [period, setPeriod] = useState("(48小时)");
    const [isNameEmpty, setIsNameEmpty] = useState(false);
    const [isIdEmpty, setIsIdEmpty] = useState(false);

    function saveChange() {
        if (userName === "") {
            setIsNameEmpty(true);
        } else {
            setIsNameEmpty(false);
        }
        if (userId.front === "" || userId.back === "") {
            setIsIdEmpty(true);
        } else {
            setIsIdEmpty(false);
        }
        if (userName !== "" && userId.front !== "" && userId.back !== "") {
            changeInfo(userName, userId, gender, period)
            gotoQrcode()
        }
    }

    function cancelChange() {
        setName("");
        setId({ front: "", back: "" });
        setGender("男");
        setPeriod("(48小时)");
        setIsNameEmpty(false);
        setIsIdEmpty(false);
        gotoQrcode();
    }

    return (
        <div>
            <Container sx={{
                marginTop: 6,
            }} maxWidth="xs">
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            id="name"
                            label="姓名"
                            variant="outlined"
                            fullWidth
                            inputProps={{
                                maxLength: 4,
                            }}
                            onChange={(e) => setName(e.target.value)}
                            error={isNameEmpty}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            id="id_front"
                            label="身份证前4位"
                            variant="outlined"
                            type="number"
                            onChange={(e) => setId({...userId, front: e.target.value.substring(0, 4).toString()})}
                            value={userId.front}
                            error={isIdEmpty}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            id="id_back"
                            label="身份证后4位"
                            variant="outlined"
                            type="number"
                            onChange={(e) => setId({ ...userId, back: e.target.value.substring(0, 4).toString() })}
                            value={userId.back}
                            error={isIdEmpty}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <FormControl>
                            <FormLabel id="gender-label">性别</FormLabel>
                            <RadioGroup
                                row
                                aria-labelledby="gender-label"
                                defaultValue="男"
                                name="gender-group"
                                onChange={(e) => setGender(e.target.value)}
                            >
                                <FormControlLabel value="男" control={<Radio />} label="男" />
                                <FormControlLabel value="女" control={<Radio />} label="女" />
                            </RadioGroup>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12}>
                        <FormControl>
                            <FormLabel id="period-label">周期</FormLabel>
                            <RadioGroup
                                row
                                aria-labelledby="period-label"
                                defaultValue="(48小时)"
                                name="period-group"
                                onChange={(e) => setPeriod(e.target.value)}
                            >
                                <FormControlLabel value="(48小时)" control={<Radio />} label="48小时" />
                                <FormControlLabel value="(72小时)" control={<Radio />} label="72小时" />
                            </RadioGroup>
                        </FormControl>
                    </Grid>
                    <Grid item xs={6}
                        sx={{
                            marginTop: 4
                        }}
                    >
                        <Button
                            variant="contained"
                            size="large"
                            name="submit"
                            fullWidth
                            onClick={saveChange}
                        >保存</Button>
                    </Grid>
                    <Grid item xs={6}
                        sx={{
                            marginTop: 4
                        }}
                    >
                        <Button
                            variant="text"
                            size="large"
                            name="cancel"
                            fullWidth
                            onClick={cancelChange}
                        >取消</Button>
                    </Grid>
                </Grid>
            </Container>

        </div>
    );
}

export default IdEditor;