import React, { useEffect, useState } from "react";
import Static from "./static";
import Qrcode from "./qrcode";
import IdEdtor from "./idEditor";
import PlaceEditor from "./placeEditor";

function App() {
	const [openTime, setOpenTime] = useState("");
	const [currentTime, setCurrentTime] = useState("0000-00-00 00:00:00");
	const [checkedTime, setCheckedTime] = useState("0000-00-00 00:00:00");

	const [currentPage, setCurrentPage] = useState("qrcode");
	const [userName, setName] = useState("王晗");
	const [userId, setId] = useState({ front: "2101", back: "4386" });
	const [gender, setGender] = useState("男");
	const [period, setPeriod] = useState("(48小时)");
	const [places, setPlaces] = useState([
		{
			location: "岐山路地铁站",
			block: "皇姑区",
		},
		{
			location: "二号线奥体中心地铁站",
			block: "浑南区",
		},
	]);

	function getDate() {
		const date = new Date();
		const year = date.getFullYear();
		const month =
			date.getMonth() + 1 < 10
				? "0" + (date.getMonth() + 1)
				: date.getMonth() + 1;
		const day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
		const hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
		const minute =
			date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
		const second =
			date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
		return (
			year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + second
		);
	}

	function yesterdayCheckTime() {
		const date = new Date();
		date.setDate(date.getDate() - 1);
		const year = date.getFullYear();
		const month =
			date.getMonth() + 1 < 10
				? "0" + (date.getMonth() + 1)
				: date.getMonth() + 1;
		const day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
		return year + "-" + month + "-" + day + " " + 22 + ":" + 16 + ":" + 20;
	}

	function renderSwitch(currentPage) {
		switch (currentPage) {
			case "static":
				return (
					<Static
						currentTime={currentTime}
						checkedTime={checkedTime}
						gender={gender}
						userName={userName}
						userId={userId}
						period={period}
					/>
				);
			case "qrcode":
				return (
					<Qrcode
						openTime={openTime}
						currentTime={currentTime}
						checkedTime={checkedTime}
						gender={gender}
						userName={userName}
						userId={userId}
						period={period}
						places={places}
						goToStatic={() => setCurrentPage("static")}
						goToIdEditor={() => setCurrentPage("idEditor")}
						goToPlaceEditor={() => setCurrentPage("placeEditor")}
					/>
				);
			case "idEditor":
				return (
					<IdEdtor
						changeInfo={changeInfo}
						gotoQrcode={() => setCurrentPage("qrcode")}
					/>
				);
			case "placeEditor":
				return (
					<PlaceEditor
						places={places}
						addPlace={addPlace}
						removePlace={removePlace}
						firstPlace={firstPlace}
						gotoQrcode={() => setCurrentPage("qrcode")}
					/>
				);
			default:
				return;
		}
	}

	function changeInfo(userName, userId, gender, period) {
		setName(userName);
		setId(userId);
		setGender(gender);
		setPeriod(period);
	}

	function addPlace(place) {
		setPlaces([place, ...places]);
	}

	function removePlace(place) {
		setPlaces(places.filter((item) => item !== place));
	}

	function firstPlace(place) {
		setPlaces([place, ...places.filter((item) => item !== place)]);
	}

	useEffect(() => {
		setOpenTime(getDate());
		setCheckedTime(yesterdayCheckTime());
		const timer = setInterval(() => {
			setCurrentTime(getDate());
		}, 1000);
		return () => clearInterval(timer);
	}, []);

	// use indexedDB to store state
	useEffect(() => {
		const request = window.indexedDB.open("state", 1);
		request.onerror = function (event) {
			console.log("Why didn't you allow my web app to use IndexedDB?!");
		};
		request.onsuccess = function (event) {
			const db = event.target.result;
			const transaction = db.transaction(["state"], "readwrite");
			const objectStore = transaction.objectStore("state");
			const request = objectStore.get("state");
			request.onerror = function (event) {
				console.log("error");
			};
			request.onsuccess = function (event) {
				if (request.result) {
					setName(request.result.userName);
					setId(request.result.userId);
					setGender(request.result.gender);
					setPeriod(request.result.period);
					setPlaces(request.result.places);
				}
			};
		};
		request.onupgradeneeded = function (event) {
			const db = event.target.result;
			if (!db.objectStoreNames.contains("state")) {
				db.createObjectStore("state", { keyPath: "id" });
			}
		};
	}, []);

	// update DBstore
	useEffect(() => {
		const request = window.indexedDB.open("state", 1);
		request.onerror = function (event) {
			console.log("Why didn't you allow my web app to use IndexedDB?!");
		};
		request.onsuccess = function (event) {
			const db = event.target.result;
			const transaction = db.transaction(["state"], "readwrite");
			const objectStore = transaction.objectStore("state");
			const request = objectStore.put({
				id: "state",
				userName: userName,
				userId: userId,
				gender: gender,
				period: period,
				places: places,
			});
			request.onerror = function (event) {
				console.log("error");
			};
			request.onsuccess = function (event) {
				console.log("success");
			};
		};
	}, [userName, userId, gender, period, places]);

	return <>{renderSwitch(currentPage)}</>;
}

export default App;
