import React, { useEffect, useState } from "react";
import {
    Container,
    TextField,
    Grid,
    FormControl,
    FormLabel,
    RadioGroup,
    FormControlLabel,
    Radio,
    Button,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    ListSubheader,
    Select,
    MenuItem,
    InputLabel,
    IconButton,
    Checkbox,

} from "@mui/material";
import DeleteIcon from '@mui/icons-material/Delete';

function PlaceEditor(props) {
    const { places, gotoQrcode, addPlace, removePlace, firstPlace } = props;
    const blocks = [...new Set(places.map((place) => place.block))];

    const [place, setPlace] = useState({
        location: "",
        block: "",
    });  
    const [deleteAbled, setDeleteAbled] = useState(false);
    const [isLocationEmpty, setIsLocationEmpty] = useState(false);
    const [isBlockEmpty, setIsBlockEmpty] = useState(false);

    useEffect(() => {
        console.log(place);
    }, [place]);

    function saveChange() {
        if (place.location === "") {
            setIsLocationEmpty(true);
        } else {
            setIsLocationEmpty(false);
        }
        if (place.block === "") {
            setIsBlockEmpty(true);
        } else {
            setIsBlockEmpty(false);
        }
        if (place.location !== "" && place.block !== "") {
            // add unique place
            if (places.every((p) => p.location !== place.location || p.block !== place.block)) {
                addPlace(place);
                gotoQrcode();
            } else {
                alert("地点已存在");
            }
            setPlace({
                location: "",
                block: "",
            });
            setIsLocationEmpty(false);
            setIsBlockEmpty(false);
        }
    }

    function cancelChange() {
        setPlace({
            location: "",
            block: "",
        });
        setDeleteAbled(false);
        setIsLocationEmpty(false);
        setIsBlockEmpty(false);
        gotoQrcode();
    }

    function handlePlaceChange(place) {
        firstPlace(place);
        gotoQrcode();
    }

    function handleRemovePlace(place) {
        removePlace(place);
    }

    return (
        <div>
            <Container sx={{
                marginTop: 4,
            }} maxWidth="xs">
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            id="location"
                            label="场所名称"
                            variant="outlined"
                            value={place.location}
                            fullWidth
                            onChange={(e) => {
                                setPlace({
                                    ...place,
                                    location: e.target.value,
                                });
                            }}
                            error={isLocationEmpty}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <InputLabel id="block-label">所在区</InputLabel>
                            <Select
                                labelId="block-label"
                                id="block"
                                value={place.block}
                                label="Age"
                                onChange={(e) => setPlace({ ...place, block: e.target.value })}
                                error={isBlockEmpty}
                            >
                                <MenuItem value={"皇姑区"}>皇姑区</MenuItem>
                                <MenuItem value={"浑南区"}>浑南区</MenuItem>
                                <MenuItem value={"大东区"}>大东区</MenuItem>
                                <MenuItem value={"和平区"}>和平区</MenuItem>
                                <MenuItem value={"沈河区"}>沈河区</MenuItem>
                                <MenuItem value={"沈北新区"}>沈北新区</MenuItem>
                                <MenuItem value={"铁西区"}>铁西区</MenuItem>
                                <MenuItem value={"于洪区"}>于洪区</MenuItem>
                                <MenuItem value={"苏家屯区"}>苏家屯区</MenuItem>
                                <MenuItem value={"辽中区"}>辽中区</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4}>
                        <Button
                            variant="contained"
                            size="large"
                            name="submit"
                            fullWidth
                            onClick={saveChange}
                        >添加</Button>
                    </Grid>
                    <Grid item xs={4}
                        sx={{
                            display: 'flex',
                            justifyContent: 'right',
                            alignItems: 'right',
                        }}
                    >
                        <FormControlLabel
                            control={
                                <Checkbox
                                    color="error"
                                    checked={deleteAbled}
                                    onChange={(event) => setDeleteAbled(event.target.checked)}
                                    sx={{
                                        color: "error.main",
                                    }}
                                />
                            }
                            label="删除"
                            // labelPlacement="start"
                            sx={{
                                color: "error.main",
                            }}

                        />
                    </Grid>
                    <Grid item xs={4}>
                        <Button
                            variant="text"
                            size="large"
                            name="submit"
                            fullWidth
                            onClick={cancelChange}
                        >返回</Button>
                    </Grid>
                </Grid>

                <List
                    sx={{
                        '& ul': { padding: 0 },
                    }}
                    subheader={<li />}
                >
                    {blocks.map((block) => (
                        <li key={`${block}`}>
                            <ul>
                                <ListSubheader>{`${block}`}</ListSubheader>
                                {
                                    places.filter((place) => place.block === block).map((place) => (
                                        <ListItem
                                            key={`${block}-${place.location}`}
                                            secondaryAction={
                                                // hide the first item
                                                deleteAbled && places.indexOf(place) !== 0 ? 
                                
                                                    <IconButton
                                                        edge="end"
                                                        aria-label="delete"
                                                        onClick={() => {
                                                            handleRemovePlace(place);
                                                        }}
                                                    >
                                                        <DeleteIcon
                                                            color="primary"
                                                        />
                                                    </IconButton> : null
                                            }
                                        >
                                            <ListItemButton
                                                onClick={() => {
                                                    handlePlaceChange(place);
                                                }}
                                            >
                                                <ListItemText primary={`${place.location}`} />
                                            </ListItemButton>
                                        </ListItem>
                                    ))                                                                        
                                }
                            </ul>
                        </li>
                    ))}
                </List>
                
            </Container>

        </div>
    );
}

export default PlaceEditor;