import React from "react";
import styles from "./qrcode.module.css";

function Qrcode(props) {
	const {
		openTime,
		currentTime,
		checkedTime,
		gender,
		userName,
		userId,
		period,
		places,
		goToStatic,
		goToIdEditor,
		goToPlaceEditor,
	} = props;
	return (
		<div className={styles.qrcode}>
			<div className={styles.top}>
				<div className={styles.empty}></div>
				<div className={styles.location} onClick={() => goToPlaceEditor()}>{places[0].location}</div>
			</div>

			<div className={styles.bottom}>
				<img className={styles.bg} src="images/bg_bottom.png" alt="bg" />
				<div className={styles.open_time}>{openTime}</div>
				<div className={styles.block}>{"辽宁省沈阳市" + places[0].block}</div>
				<div className={styles.time}>{currentTime}</div>
				<div className={styles.record}>{checkedTime}</div>
				{gender === "男" ? (
					<img className={styles.gender} src="images/male.png" alt="male" />
				) : (
					<img className={styles.gender} src="images/female.png" alt="female" />
				)}
				<div className={styles.name} onClick={()=> goToIdEditor()}>{"*" + userName.substring(1)}</div>
				<div className={styles.id}>{userId.front + "********" + userId.back}</div>
				<div className={styles.period}>{period}</div>
			</div>

			<img
				className={styles.fix}
				src="images/change.png"
				alt="fix"
				onClick={() => goToStatic()}
			/>
		</div>
	);
}

export default Qrcode;
