import React from "react";
import styles from "./static.module.css"

function Static(props) {
	const { currentTime, checkedTime, gender, userName, userId, period} = props;
	return (
		<div className={styles.static}>
			<img className={styles.bg} src="images/bg.png" alt="bg" />
			<div className={styles.time}>{currentTime}</div>
			<div className={styles.record}>{checkedTime}</div>
			{gender === "男" ? (
				<img className={styles.gender} src="images/male.png" alt="male" />
			) : (
				<img className={styles.gender} src="images/female" alt="female" />
			)}
			<div className={styles.name}>
				{"*" + userName.substring(1)}
			</div>
			<div className={styles.id}>{userId.front + "********" + userId.back}</div>
			<div className={styles.period}>{period}</div>
			<img className={styles.fix} src="images/fix.png" alt="fix" />
		</div>
	);
}

export default Static;
